<template>
  <button :class="classify" :disabled="disabled || loading">
    <!-- axe- -->
    <i :class="['icon', icon]" v-if="icon && !loading" />
    <i class="icon axe-icon-loading" v-if="!icon && loading" />
    <span v-if="$slots.default">
      <slot></slot>
    </span>
  </button>
</template>

<script>
import { computed } from 'vue'
const typeArray = ['default', 'primary', 'success', 'info', 'warning', 'danger']
const positionArray = ['left', 'right']
export default {
  name: 'AxeButton', // namenameaxe-<axe-button>
  props: {
    type: {
      type: String,
      default: 'default',
      validator(type) {
        if (!typeArray.includes(type)) {
          throw Error(
            'type' +
              typeArray.join('') +
              ''
          )
        }
        return true
      }
    },
    icon: String,
    loading: Boolean,
    position: {
      type: String,
      default: 'left',
      require: false,
      validator(type) {
        if (!positionArray.includes(type)) {
          throw Error(`
            position
            ${positionArray.join('')}
            
          `)
        }
      }
    },
    disabled: Boolean
  },
  setup(props) {
    const classify = computed(() => [
      'axe-button',
      `axe-button-${props.type}`,
      props.icon && `axe-button-${props.position}`
    ])
    return {
      classify
    }
  }
}
</script>
